* { -webkit-box-sizing: border-box; box-sizing: border-box; }
    html, body, #human-body-root {
      margin: 0; width: 100vw; height: 100vh;
      font-family: 'Helvetica';
      background: -webkit-radial-gradient(center ellipse, rgba(147,206,222,1) 0%, rgba(117,189,209,1) 48%, rgba(73,129,189,1) 100%);
      background: -o-radial-gradient(center ellipse, rgba(147,206,222,1) 0%, rgba(117,189,209,1) 48%, rgba(73,129,189,1) 100%);
      background: radial-gradient(ellipse at center, rgba(147,206,222,1) 0%, rgba(117,189,209,1) 48%, rgba(73,129,189,1) 100%);
    }
    body {
      padding: 0px;
    }
    #human-body-highlights > svg {
      position: absolute;
      cursor: pointer;
      opacity: 0;
      -webkit-transition: opacity 0.3s ease;
      -o-transition: opacity 0.3s ease;
      transition: opacity 0.3s ease;
    }
    #human-body-root {
      position: relative;
      padding: 25px;
    }
